* {
  -webkit-tap-highlight-color: transparent;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;

  background-color: #ffffff;
  font-family: sans-serif;

  overflow: hidden;
}

#root {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 430px;
  height: 100vh;

  background: white;

  text-align: center;
  padding: 24px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  justify-content: center;
}


/* =========================
   로고
========================= */

.logo {
  width: 60%;
  max-width: 220px;
  margin: 0 auto 20px;
}


/* =========================
   START 버튼
========================= */

.start-button {
  background-color: black;
  color: white;
  border: none;
  border-radius: 20px;

  padding: 12px 26px;
  font-size: 15px;

  cursor: pointer;
  transition: all 0.2s ease;

  width: auto;
  align-self: center;
}

.start-button:hover {
  background-color: #333;
  transform: scale(1.05);
}

.start-button:active {
  transform: scale(0.96);
}


/* =========================
   SNS 링크
========================= */

.sns-links {
  margin-top: 14px;
  text-align: center;
  font-size: 13px;
}

.sns-row {
  display: flex;
  justify-content: center;
  gap: 18px;
  margin: 2px 0;
}

.sns-links a {
  color: #888;
  text-decoration: none;
}

.sns-links a:hover {
  color: black;
}


/* =========================
   버튼
========================= */

button {
  width: 90%;
  padding: 14px;
  margin: 8px auto;

  font-size: 15px;

  border-radius: 12px;
  border: none;
  background-color: #f3f3f3;

  cursor: pointer;
  transition: transform 0.08s ease,
              box-shadow 0.08s ease,
              background-color 0.2s;
}

button:hover {
  background-color: #e5e5e5;
}

button:active {
  transform: scale(0.96);
}

.answers {
  display: flex;
  flex-direction: column;
}


/* =========================
   진행도
========================= */

.progress {
  margin-top: 16px;
  font-size: 13px;
  color: gray;
}


/* =========================
   로딩 스피너
========================= */

.loading-spinner {
  width: 50px;
  height: 50px;

  border: 5px solid #eee;
  border-top: 5px solid black;
  border-radius: 50%;

  margin: 0 auto;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 480px) {
  html, body {
    background: white;
  }

  .container {
    max-width: 100%;
    padding: 20px;
  }

  .logo {
    width: 70%;
  }
}